
import { defineComponent, ref, PropType } from 'vue'
import { headerItems } from '../types'
import dayjs from 'dayjs'
import { formatUTCDate } from '@/utils/date-format'
export default defineComponent({
  name: 'BaseTable',
  props: {
    tableData: {
      type: Array,
      required: true
    },
    headerItems: {
      type: Array as PropType<headerItems[]>,
      default() {
        return []
      }
    },
    showCheckColumn: {
      type: Boolean,
      default: true
    },
    showIndexColumn: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: ''
    },
    totalCount: {
      type: Number,
      default: 0
    }
  },
  emits: ['sizeChange', 'offsetChange'],
  computed: {
    getDate() {
      return function (value, dataFormat) {
        // const format =
        //   typeof dataFormat === 'string' ? dataFormat : 'YYYY-MM-DD HH:mm:ss'
        // return dayjs(value).format(format)
        return formatUTCDate(value)
      }
    }
  },
  setup(props, { emit }) {
    const pageSize = ref(10)
    const currentPage = ref(1)
    const handleSizeChange = (value) => {
      pageSize.value = value
      emit('sizeChange', value)
    }
    const handleCurrentChange = (value) => {
      currentPage.value = value
      emit('offsetChange', value)
    }
    return {
      dayjs,
      currentPage,
      pageSize,
      handleSizeChange,
      handleCurrentChange
    }
  }
})
