
import { defineComponent, PropType, ref, watch, reactive } from 'vue'
import { IFormItem } from '../types'
import BaseSelect from '@/base-ui/select'
import { ElForm } from 'element-plus'
export default defineComponent({
  name: 'BaseForm',
  props: {
    formItems: {
      type: Array as PropType<IFormItem[]>,
      required: true
    },
    labelWidth: {
      type: String,
      default: '100px'
    },
    itemStyles: {
      type: Object,
      default() {
        return {}
      }
    },
    colLayout: {
      type: Object,
      default() {
        return {
          xl: 6,
          lg: 8,
          md: 12,
          sm: 24,
          xs: 24
        }
      }
    },
    formObject: {
      type: Object,
      required: true
    },
    size: {
      type: String,
      default: 'medium '
    }
  },
  computed: {
    selectConfig() {
      return (item) => {
        return Object.assign(item.select, {
          value: this.formObject[item.value]
        })
      }
    }
  },
  components: { BaseSelect },
  setup(props: any, { emit }) {
    const form = ref<InstanceType<typeof ElForm>>()
    let formData = reactive(props.formObject)
    // 监听formData 触发父组件更新数据
    watch(
      formData,
      (newVal) => {
        emit('update', newVal)
      },
      { deep: true }
    )
    const resetField = () => {
      form.value?.resetFields()
    }
    const validate = () => {
      return new Promise((resolve, reject) => {
        form.value?.validate((valid) => {
          if (valid) {
            resolve(true)
          } else {
            reject(false)
          }
        })
      })
    }
    // select change
    const handleSelectChange = (value, row) => {
      formData[row.value] = value
    }
    return { formData, resetField, form, handleSelectChange, validate }
  },
  emits: ['update']
})
